/* full height mobile fix */
:root {
  --app-height: 100%;
}

* {
  box-sizing: border-box;
  outline: none !important;
}
*::before, *::after {
  box-sizing: border-box;
}

body,
h1, h2, h3, h4, h5, h6, p,
ul[class], ol[class], li,
figure, figcaption, blockquote,
dl, dd {
  margin: 0;
}

ul[class], ol[class] {
  padding: 0;
  list-style-type: none;
}

a:not([class]) {
  text-decoration-skip-ink: auto;
  color: inherit;
}
a[class] {
  text-decoration: none;
}

input, button, textarea, select {
  font: inherit;
}

button, label {
  cursor: pointer;
}

body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  min-width: 320px;
  position: relative;
  font-family: Montserrat, sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 22px;
  color: #565656;
  background-color: #FFF;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  overflow-x: hidden;
}
body::-webkit-scrollbar {
  width: 6px;
  height: 6px;
  border-radius: 0;
}
body::-webkit-scrollbar-thumb {
  background: #A8A8A8;
  border-radius: 0;
  transition: background-color 0.3s;
}
body::-webkit-scrollbar-thumb:hover {
  background: #565656;
}
body::-webkit-scrollbar-track {
  background: #E9E9E9;
  border-radius: 0;
}
body {
  scrollbar-face-color: #A8A8A8;
  scrollbar-track-color: #E9E9E9;
}
body.overflow {
  overflow: hidden;
}
body.overflow:after {
  content: "";
  display: block;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.6);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9;
}

.container {
  width: 100%;
  margin: 0 auto;
  max-width: 1190px;
  padding: 0 16px;
}
@media (min-width: 1200px) {
  .container {
    padding: 0 15px;
  }
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10;
}
.header .container {
  max-width: 1440px;
}
@media (min-width: 1200px) {
  .header .container {
    padding: 0 24px;
  }
}
.header__stock {
  background-color: #FFF;
  font-weight: 600;
  font-size: 14px;
  line-height: 14px;
  text-align: center;
  text-transform: uppercase;
  padding: 10px 0 6px;
  overflow: hidden;
  text-overflow: ellipsis;
  transition: all 0.3s;
}
@media (max-width: 1190px) {
  .header__stock {
    font-size: calc(10px + 4 *((100vw - 340px) / 850));
  }
}
@media (max-width: 1190px) {
  .header__stock {
    line-height: calc(12px + 2 *((100vw - 340px) / 850));
  }
}
@media (min-width: 768px) {
  .header__stock {
    padding: 8px 0;
  }
}
.header__main {
  background: rgba(86, 86, 86, 0.98);
  transition: background 0.3s;
  padding: 16px 0;
  /* settings */
  /* slide animate */
}
@media (min-width: 1200px) {
  .header__main {
    padding: 14px 0;
  }
}
.header__main .container {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-wrap: wrap;
}
.header__main .hamburger {
  padding: 8px 5px;
  display: inline-block;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition-property: opacity, filter;
  transition-duration: 0.15s;
  transition-timing-function: linear;
  font: inherit;
  color: inherit;
  text-transform: none;
  background-color: transparent;
  border: 0;
  overflow: visible;
  position: relative;
  width: 32px;
  height: 32px;
  border-radius: 50%;
  z-index: 20;
  margin: 0 16px 0 0;
}
@media (min-width: 1200px) {
  .header__main .hamburger {
    display: none;
  }
}
.header__main .hamburger:hover {
  opacity: 1;
}
.header__main .hamburger.is-active:hover {
  opacity: 1;
}
.header__main .hamburger.is-active .hamburger-inner {
  background-color: #fff;
}
.header__main .hamburger.is-active .hamburger-inner::before, .header__main .hamburger.is-active .hamburger-inner::after {
  background-color: #fff;
}
.header__main .hamburger-box {
  width: 22px;
  height: 16px;
  display: inline-block;
  position: relative;
}
.header__main .hamburger-inner {
  display: block;
  top: 50%;
  width: 22px;
  height: 1.5px;
  background-color: #fff;
  border-radius: 30px;
  position: absolute;
  transition-timing-function: ease;
  transition: transform 0.15s;
}
.header__main .hamburger-inner::before, .header__main .hamburger-inner::after {
  width: 22px;
  height: 1.5px;
  background-color: #fff;
  border-radius: 30px;
  position: absolute;
  transition-property: transform;
  transition-duration: 0.15s;
  transition-timing-function: ease;
}
.header__main .hamburger-inner::before, .header__main .hamburger-inner::after {
  content: "";
  display: block;
}
.header__main .hamburger-inner::before {
  top: -10px;
}
.header__main .hamburger--slider .hamburger-inner {
  top: 0;
}
.header__main .hamburger--slider .hamburger-inner::before {
  top: 8px;
  right: 0;
  width: 22px;
  transition-property: transform, opacity;
  transition-timing-function: ease;
  transition-duration: 0.15s;
}
.header__main .hamburger--slider .hamburger-inner::after {
  top: 16px;
}
.header__main .hamburger--slider.is-active .hamburger-inner {
  transform: translate3d(-2px, 8px, 0) rotate(45deg);
  height: 2px;
  width: 25px;
}
.header__main .hamburger--slider.is-active .hamburger-inner::before {
  transform: rotate(-45deg) translate3d(-5.71429px, -6px, 0);
  opacity: 0;
}
.header__main .hamburger--slider.is-active .hamburger-inner::after {
  transform: translate3d(0, -16px, 0) rotate(-90deg);
  height: 2px;
  width: 25px;
}
.header__main .logo-link {
  display: inline-block;
  flex: 0 0 auto;
  padding: 9px 0;
}
.header__main .logo-link .logo-image {
  display: block;
  width: auto;
  max-width: 100%;
  margin: 0;
}
@media (min-width: 1200px) {
  .header__main .city-link {
    display: inline-block !important;
    text-transform: uppercase;
    color: #fff;
    font-weight: 500;
    font-size: 14px;
    line-height: 14px;
    position: relative;
    padding-left: 23px;
    margin-left: 25px;
  }
  .header__main .city-link:before {
    content: "\e813";
    font-family: Fontello;
    font-style: normal;
    font-weight: normal;
    speak: never;
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    margin: 0;
    text-decoration: inherit;
    text-align: center;
    font-variant: normal;
    text-transform: none;
  }
}
.header__main .hmain-menu {
  position: absolute;
  top: 0;
  left: 0;
  width: 328px;
  height: var(--app-height);
  transition: transform 0.3s;
  background-color: #565656;
  padding: 46px 14px 0 0;
  z-index: 10;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}
@media (min-width: 1200px) {
  .header__main .hmain-menu {
    flex-direction: row;
    position: initial !important;
    width: auto;
    height: auto;
    flex: 1 1 auto;
    padding: 0 0 0 4.5%;
    transform: none !important;
    background-color: transparent;
  }
}
.header__main .hmain-menu__header {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  flex-wrap: wrap;
  padding: 3px;
  flex: 0 0 24px;
  width: 100%;
}
@media (min-width: 1200px) {
  .header__main .hmain-menu__header {
    display: none !important;
  }
}
.header__main .hmain-menu__header .b2b-link {
  flex: 0 0 auto;
  width: auto;
  padding: 5px 12px;
  border: 1px solid #fff;
  text-align: center;
  border-radius: 30px;
  color: #fff;
  font-weight: 500;
  font-size: 12px;
  line-height: 12px;
  transition: opacity 0.3s;
}
.header__main .hmain-menu__header .b2b-link:focus {
  opacity: 0.5;
}
.header__main .hmain-menu__header .favorite-link {
  color: #fff;
  font-size: 24px;
  line-height: 24px;
  display: inline-block;
  margin-left: 15px;
  transition: opacity 0.3s;
}
.header__main .hmain-menu__header .favorite-link:focus {
  opacity: 0.5;
}
.header__main .hmain-menu__categories {
  margin-top: 17px;
  width: calc(100% + 14px);
  max-width: calc(100% + 14px);
  flex: 0 0 128px;
}
@media (min-width: 1200px) {
  .header__main .hmain-menu__categories {
    margin: 0;
    flex: 1 1 auto;
    width: auto;
    max-width: 100%;
  }
}
.header__main .hmain-menu__categories .categories-list {
  display: flex;
  align-items: stretch;
  justify-content: flex-start;
  max-width: 100%;
}
@media (min-width: 1200px) {
  .header__main .hmain-menu__categories .categories-list {
    align-items: center;
    justify-content: center;
  }
}
.header__main .hmain-menu__categories .categories-list__item {
  width: 100%;
  flex: 1 1 42%;
  transition: all 0.3s;
}
@media (min-width: 1200px) {
  .header__main .hmain-menu__categories .categories-list__item {
    flex: 0 0 auto !important;
    width: auto;
    margin: 0 12px;
  }
}
.header__main .hmain-menu__categories .categories-list__item.active {
  flex: 1 1 60%;
}
.header__main .hmain-menu__categories .categories-list__item.active .categories-list__link span {
  padding: 12px 16px;
}
@media (min-width: 120px) {
  .header__main .hmain-menu__categories .categories-list__item.active .categories-list__link span {
    padding: 10px;
  }
}
.header__main .hmain-menu__categories .categories-list__item.active .categories-list__link:after {
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.1));
}
@media (min-width: 1200px) {
  .header__main .hmain-menu__categories .categories-list__item.active .categories-list__link:after {
    display: none;
  }
}
.header__main .hmain-menu__categories .categories-list__link {
  display: block;
  position: relative;
  width: 100%;
  height: 100%;
  min-height: 128px;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top center;
}
@media (min-width: 1200px) {
  .header__main .hmain-menu__categories .categories-list__link {
    min-height: auto;
    width: auto;
    height: auto;
    background-image: none !important;
  }
}
.header__main .hmain-menu__categories .categories-list__link span {
  position: absolute;
  bottom: 0;
  left: 0;
  padding: 12px 10px;
  text-transform: uppercase;
  color: #fff;
  z-index: 1;
  font-weight: 600;
  font-size: 12px;
