@import "../settings"
.header
  position: fixed
  top: 0
  left: 0
  width: 100%
  z-index: 10
  .container
    max-width: 1440px
    @media (min-width: 1200px)
      padding: 0 24px
  &__stock
    background-color: $bg-color
    font-weight: 600
    @include adaptive-value('font-size', 14, 10, 1)
    @include adaptive-value('line-height', 14, 12, 1)
    text-align: center
    text-transform: uppercase
    padding: 10px 0 6px
    overflow: hidden
    text-overflow: ellipsis
    transition: all .3s
    @media (min-width: 768px)
      padding: 8px 0
  &__main
    background: rgba($dark-gray, .98)
    transition: background .3s
    padding: 16px 0
    @media (min-width: 1200px)
      padding: 14px 0
    .container
      display: flex
      align-items: center
      justify-content: flex-start
      flex-wrap: wrap
    @import "hamburger"
    .logo-link
      display: inline-block
      flex: 0 0 auto
      padding: 9px 0
      .logo-image
        display: block
        width: auto
        max-width: 100%
        margin: 0
    .city-link
      @media (min-width: 1200px)
        display: inline-block !important
        text-transform: uppercase
        color: #fff
        font-weight: 500
        font-size: 14px
        line-height: 14px
        position: relative
        padding-left: 23px
        margin-left: 25px
        &:before
          content: '\e813'
          font-family: Fontello
          font-style: normal
          font-weight: normal
          speak: never
          display: block
          position: absolute
          top: 0
          left: 0
          margin: 0
          text-decoration: inherit
          text-align: center
          font-variant: normal
          text-transform: none
    .hmain-menu
      position: absolute
      top: 0
      left: 0
      width: 328px
      height: var(--app-height)
      transition: transform .3s
      background-color: $dark-gray
      padding: 46px 14px 0 0
      z-index: 10
      display: flex
      flex-direction: column
      align-items: flex-start
      justify-content: flex-start
      @media (min-width: 1200px)
        flex-direction: row
        position: initial !important
        width: auto
        height: auto
        flex: 1 1 auto
        padding: 0 0 0 4.5%
        transform: none !important
        background-color: transparent
      &__header
        display: flex
        align-items: center
        justify-content: flex-end
        flex-wrap: wrap
        padding: 3px
        flex: 0 0 24px
        width: 100%
        @media (min-width: 1200px)
          display: none !important
        .b2b-link
          flex: 0 0 auto
          width: auto
          padding: 5px 12px
          border: 1px solid #fff
          text-align: center
          border-radius: 30px
          color: #fff
          font-weight: 500
          font-size: 12px
          line-height: 12px
          transition: opacity .3s
          &:focus
            opacity: .5
        .favorite-link
          color: #fff
          font-size: 24px
          line-height: 24px
          display: inline-block
          margin-left: 15px
          transition: opacity .3s
          &:focus
            opacity: .5
      &__categories
        margin-top: 17px
        width: calc(100% + 14px)
        max-width: calc(100% + 14px)
        flex: 0 0 128px
        @media (min-width: 1200px)
          margin: 0
          flex: 1 1 auto
          width: auto
          max-width: 100%
        .categories-list
          display: flex
          align-items: stretch
          justify-content: flex-start
          max-width: 100%
          @media (min-width: 1200px)
            align-items: center
            justify-content: center
          &__item
            width: 100%
            flex: 1 1 42%
            transition: all .3s
            @media (min-width: 1200px)
              flex: 0 0 auto !important
              width: auto
              margin: 0 12px
            &.active
              flex: 1 1 60%
              .categories-list__link
                span
                  padding: 12px 16px
                  @media (min-width: 120px)
                    padding: 10px
                &:after
                  background: linear-gradient(0deg,rgba(0,0,0,.4),rgba(0,0,0,.1))
                  @media (min-width: 1200px)
                    display: none
          &__link
            display: block
            position: relative
            width: 100%
            height: 100%
            min-height: 128px
            background-size: cover
            background-repeat: no-repeat
            background-position: top center
            @media (min-width: 1200px)
              min-height: auto
              width: auto
              height: auto
              background-image: none !important
            span
              position: absolute
              bottom: 0
              left: 0
              padding: 12px 10px
              text-transform: uppercase
              color: #fff
              z-index: 1
              font-weight: 600
              font-size: 12px
              line-height: 14px
              transition: padding .3s
              max-width: 150px
              @media (min-width: 1200px)
                position: relative
                max-width: 100%
                font-size: 14px
                line-height: 14px
                padding: 10px
            &:after
              content: ''
              display: block
              width: 100%
              height: 100%
              position: absolute
              top: 0
              left: 0
              background: linear-gradient(0deg,rgba(0,0,0,.6),rgba(0,0,0,.3))
              transition: background .3s
              @media (min-width: 1200px)
                display: none !important
      &__subcategories
        overflow-y: scroll
        flex: 1 1 auto
        width: 100%
        padding-right: 16px
        min-height: 209px
        margin: 20px 0
        position: relative
        @include scrollbars(3px, rgba($medium-gray,.2), #fff, #fff, 0)
        @media (min-width: 1200px)
          padding: 0
          min-height: auto
          margin: 0
          overflow: visible
          position: absolute
          top: 100%
          left: 0
          width: 100vw
          height: 48px
          border-top: 1px solid rgba(#f2f2f2, .2)
          background-color: rgba(86,86,86,.98)
          transition: background .3s
        .subcategories-list
          padding: 0 0 0 16px
          transform: translateX(-100vw)
          transition: transform .3s
          position: absolute
          top: 0
          left: 0
          width: 100%
          height: auto
          max-height: 100%
          max-width: calc(100% - 16px)
          @media (min-width: 1200px)
            display: none
            flex-wrap: wrap
            align-items: center
            justify-content: center
            width: auto
            max-width: initial
            padding: 0
            transform: none
            margin: 0 auto
            left: 0
            right: 0
          &.show
            transform: translateX(0)
            @media (min-width: 1200px)
              transform: none
              display: flex
          &__link
            display: flex
            align-items: center
            justify-content: space-between
            color: #fff
            text-transform: uppercase
            padding: 10px 0
          &__item
            border-top: 1px solid rgba(#fff, .5)
            @media (min-width: 1200px)
              border: none
              margin: 0 20px
              position: initial
            &:nth-of-type(1)
              border-top: none
            &.submenu
              .subcategories-list__link
                position: relative
                line-height: 20px
                font-weight: 500
                border-bottom: 1px solid transparent
                transition: border-color .3s
                @media (min-width: 1200px)
                  padding: 13px 0
                &:after
                  content: '\e818'
                  font-family: Fontello
                  font-style: normal
                  font-weight: normal
                  speak: never
                  display: block
                  margin: 0
                  text-decoration: inherit
                  text-align: center
                  font-variant: normal
                  text-transform: none
                  font-size: 20px
                  line-height: 20px
                  flex: 0 0 auto
                  transition: transform .3s
                  @media (min-width: 1200px)
                    display: none
                &.active
                  border-bottom: 1px solid rgba(#fff, .1)
                  @media (min-width: 1200px)
                    border: none
                  &:after
                    transform: scale(1,-1)
              .submenu-wrapper
                max-height: 0
                overflow-y: hidden
                transition: max-height .5s
                @media (min-width: 1200px)
                  position: absolute
                  top: 100%
                  left: 0
                  width: 100vw
                &.show
                  max-height: 100vh
                  @media (min-width: 1200px)
                    max-height: 0
              .submenu-list
                @media (min-width: 1200px)
                  display: block
                  column-count: 3
                  // tmp
                  // display: flex tmp
                  // flex-direction: column
                  // align-items: flex-start
                  // justify-content: flex-start
                  // flex-wrap: wrap
                  background-color: rgba($dark-gray,.98)
                  width: 100vw
                  padding: 20px calc((100vw - 1190px) / 2 + 15px)
                &__item
                  margin: 16px 0
                  @media (min-width: 1200px)
                    margin: 0 0 4px 0
                  &:nth-of-type(1)
                    margin-top: 8px
                    @media (min-width: 1200px)
                      margin-top: 0
                &__link
                  color: #fff
                  font-weight: 500
                  font-size: 14px
                  line-height: 14px
                  letter-spacing: .01em
                  padding: 10px 0
                  @media (min-width: 1200px)
                    display: inline-block
                  &.active
                    opacity: .5
                  &:hover
                    text-decoration: underline
              &:hover
                .submenu-wrapper
                  @media (min-width: 1200px)
                    max-height: 100vh
                    transition: max-height .5s ease-in-out .3s
      &__footer
        display: flex
        flex-direction: column
        align-items: flex-start
        justify-content: flex-start
        width: calc(100% + 14px)
        padding: 20px 16px
        border-top: 1px solid rgba($medium-gray, .2)
        margin-top: auto
        @media (min-width: 1200px)
          display: none
        .phone-link
          display: inline-block
          color: #fff
          font-weight: 600
          font-size: 14px
          line-height: 14px
          letter-spacing: .02em
        .city-link
          display: inline-block
          text-transform: uppercase
          color: #fff
          font-weight: 500
          font-size: 14px
          line-height: 14px
          position: relative
          padding-left: 23px
          margin-top: 25px
          &:before
            content: '\e813'
            font-family: Fontello
            font-style: normal
            font-weight: normal
            speak: never
            display: block
            position: absolute
            top: 0
            left: 0
            margin: 0
            text-decoration: inherit
            text-align: center
            font-variant: normal
            text-transform: none
      &.show
        transform: translateX(0) !important
    .phone-link
      @media (min-width: 1200px)
        display: inline-block !important
        color: #fff
        font-weight: 600
        font-size: 14px
        line-height: 14px
        letter-spacing: .02em
        margin-right: 10px
    .search-block
      margin: 0 0 0 auto
      @media (min-width: 1200px)
        margin: 0 20px 0 0
        position: relative
        overflow: hidden
        transition: margin .3s
        &.open
          overflow: visible
      .search-link
        display: inline-block
        padding: 4px
        color: #fff
        font-size: 24px
        line-height: 24px
      &__form
        display: flex
        align-items: center
        justify-content: flex-start
        .input-group
          background-color: #fff
          border: 1px solid rgba($dark-gray, .5)
          display: flex
          align-items: stretch
          justify-content: flex-start
          flex-wrap: nowrap
          padding: 11px 12px
          flex: 1 1 calc(100% - 40px)
          max-width: calc(100% - 40px)
          @media (min-width: 1200px)
            flex: 1 1 100%
            max-width: 100%
            padding: 9px 10px
            border: none
          button
            flex: 0 0 20px
            width: 20px
            height: 20px
            display: flex
            align-items: center
            justify-content: center
            background-color: transparent
            border: none
            color: $dark-gray
            font-size: 20px
            line-height: 20px
            margin-right: 6px
          input
            flex: 1 1 100%
            width: 100%
            border: none
            font-size: 14px
            line-height: 14px
            letter-spacing: -.04em
            background-color: transparent
            color: #000
            @include input-placeholder
              color: rgba($dark-gray, .5)
        .close-link
          display: inline-block
          color: #fff
          font-size: 30px
          line-height: 30px
          margin-left: 9px
          @media (min-width: 1200px)
            display: none
      &__result
        display: flex
        flex-direction: column
        transition: transform .3s
        background-color: $dark-gray
        width: 100vw
        height: var(--app-height)
        overflow: scroll
        position: absolute
        top: 0
        left: 0
        z-index: 21
        padding: 16px
        @media (min-width: 640px)
          max-width: 420px
          left: auto
          right: 0
          padding: 38px 16px 16px
        @media (min-width: 1200px)
          background-color: transparent
          padding: 0
          overflow: hidden
          max-width: 373px
          height: auto
          top: -2px
          transition: width .3s
          transform: none
          width: 0
        &.show
          transform: translateX(0) !important
          @media (min-width: 1200px)
            width: 373px
        .product-item
          display: flex
          align-items: stretch
          justify-content: flex-start
          margin-top: 16px
          @media (min-width: 1200px)
            margin-top: 8px
          &:nth-of-type(1)
            margin-top: 20px
            @media (min-width: 1200px)
              margin-top: 12px
          .image-block
            flex: 0 0 71px
            max-width: 71px
            .product-link
              display: block
            .product-image
              display: block
              width: auto
              max-width: 100%
              margin: 0
          .descr-block
            display: flex
            flex-direction: column
            align-items: flex-start
            justify-content: flex-start
            flex: 1 1 100%
            padding-left: 16px
            @media (min-width: 1200px)
              min-width: 150px
            .product-name
              color: #fff
              text-transform: uppercase
              font-weight: 500
              font-size: 12px
              line-height: 12px
              letter-spacing: .02em
              @media (min-width: 1200px)
                color: $dark-gray
            .article
              display: inline-block
              margin-top: 8px
              font-weight: 500
              font-size: 12px
              line-height: 12px
              letter-spacing: .05em
              color: rgba(#fff, .5)
              @media (min-width: 1200px)
                color: rgba($dark-gray, .5)
            .properties
              display: flex
              flex-wrap: wrap
              align-items: flex-start
              justify-content: flex-start
              font-size: 12px
              line-height: 16px
              color: #fff
              margin-top: 16px
              @media (min-width: 1200px)
                color: $dark-gray
              & > span
                margin-left: 16px
                &:nth-of-type(1)
                  margin-left: 0
              &__item
                flex: 0 0 auto
                width: auto
              &__color
                flex: 0 0 auto
                display: flex
                align-items: center
                justify-content: flex-start
                width: auto
                position: relative
                span
                  position: relative
                  width: 16px
                  height: 16px
                  display: inline-block
                  border-radius: 50%
                  border: 3px solid $dark-gray
                  margin-right: 10px
                  @media (min-width: 1200px)
                    border: 3px solid #fff
                  &:before
                    content: ''
                    display: block
                    width: 18px
                    height: 18px
                    position: absolute
                    top: -4px
                    left: -4px
                    background-color: inherit
                    border-radius: 50%
                    z-index: -1
                    background-color: inherit
                    opacity: .5
          .price-block
            flex: 0 0 auto
            width: auto
            color: #fff
            font-size: 12px
            line-height: 12px
            font-weight: 600
            @media (min-width: 1200px)
              color: $dark-gray
        .result-catalog
          padding: 9px 0
          @media (min-width: 1200px)
            background-color: #fff
            margin-top: 6px
            padding: 0 24px
            .products-wrapper
              width: calc(100% + 15px)
              padding: 0 12px 0 0
              margin-top: 12px
              max-height: 208px
              overflow-y: scroll
              @include scrollbars(3px, $light-gray, $medium-gray, $medium-gray, 0)
          .product-item
            &:last-child
              border-bottom: 1px solid rgba($medium-gray, .5)
              padding-bottom: 16px
              @media (min-width: 1200px)
                border-bottom: none
        .result-info
          @media (min-width: 1200px)
            background-color: #fff
            padding: 18px 24px
            overflow: hidden
          .info-title
            text-transform: uppercase
            font-weight: 500
            font-size: 12px
            line-height: 12px
            color: rgba(#fff, .5)
            @media (min-width: 1200px)
              color: rgba($dark-gray, .5)
              white-space: nowrap
          &__actual
            padding-bottom: 3px
            .actual-list
              margin: 24px 0
              padding: 0
              &__item
                width: 100%
                margin-top: 24px
                &:nth-of-type(1)
                  margin-top: 0
              &__link
                display: inline-block
                color: #fff
                font-weight: 500
                font-size: 14px
                line-height: 14px
                @media (min-width: 1200px)
                  color: $dark-gray
                  white-space: nowrap
          &__yousee
            padding: 18px 0
            border-top: 1px solid rgba($medium-gray, .5)
            @media (min-width: 1200px)
              border-top: 1px solid $light-gray
              padding: 10px 0 0
            .products-wrapper
              @media (min-width: 1200px)
                width: calc(100% + 15px)
                padding: 0 12px 0 0
                margin-top: 12px
                max-height: 208px
                overflow-y: scroll
                @include scrollbars(3px, $light-gray, $medium-gray, $medium-gray, 0)
                .product-item:nth-of-type(1)
                  margin-top: 0
            .see-all
              display: inline-flex
              align-items: center
              justify-content: flex-start
              position: relative
              color: #fff
              font-weight: 600
              font-size: 14px
              line-height: 14px
              margin-top: 32px
              @media (min-width: 1200px)
                color: $dark-gray
                margin-top: 24px
                white-space: nowrap
              .icon
                transform: scale(-1,1)
                margin-right: 5px
        .result-copyright
          margin-top: auto
          border-top: 1px solid rgba($medium-gray, .5)
          padding: 20px 0 5px
          font-size: 12px
          line-height: 12px
          font-weight: 500
          letter-spacing: .05em
          text-transform: uppercase
          color: rgba(#fff, .5)
          @media (min-width: 1200px)
            background-color: #fff
            padding: 15px 24px
            color: rgba($dark-gray, .5)
            white-space: nowrap
    .b2b-block
      @media (min-width: 1200px)
        display: inline-block !important
        margin-right: 7px
        .b2b-link
          flex: 0 0 auto
          width: auto
          padding: 3px 12px
          border: 1px solid #fff
          text-align: center
          border-radius: 30px
          color: #fff
          font-weight: 500
          font-size: 12px
          line-height: 12px
          transition: opacity .3s
          &:focus
            opacity: .5
    .profile-links
      display: flex
      align-items: center
      justify-content: flex-end
      margin: 0
      position: relative
      right: -4px
      &__item
        margin-left: 7px
        @media (min-width: 1200px)
          display: inline-block !important
          position: relative
          margin-left: 9px
        .profile-menu
          background-color: #fff
          padding: 43px 15px 43px 36px
          right: -12px
          top: calc(100% + 16px)
          transition: transform .3s
          width: 200px
          border: 1px solid $light-gray
          @media (min-width: 1200px)
            width: 0
            top: calc(100% + 13px)
            transform: none
            transition: width .3s, padding-left .3s, padding-right .3s
            overflow: hidden
            padding: 0
            z-index: 10
          &.show
            transform: translateX(0) !important
            @media (min-width: 1200px)
              width: 201px
              padding: 20px 20px 20px 35px
          &__item
            display: block
            width: 100%
            margin-top: 16px
            &:nth-of-type(1)
              margin-top: 0
          &__link
            color: $dark-gray
            font-size: 14px
            line-height: 14px
            font-weight: 500
            text-transform: uppercase
            position: relative
            @media (min-width: 1200px)
              white-space: nowrap
            &.active
              &:before
                content: ''
                display: block
                width: 5px
                height: 5px
                border-radius: 50%
                background-color: $red-color
                position: absolute
                top: 6px
                left: -20px
            &.exit-link
              color: $medium-gray
              transition: color .3s
              &:hover
                color: $dark-gray
        .minicart-content
          width: 100vw
          max-width: 510px
          background-color: #fff
          transition: transform .3s
          top: -44px
          right: -12px
          z-index: 20
          padding: 22px 16px
          height: 100vh
          overflow-y: scroll
          @include scrollbars(3px, rgba($medium-gray,.2), #fff, #fff, 0)
          @media (min-width: 1200px)
            position: fixed !important
            top: 0
            right: 0
            padding: 30px 24px
          .minicart-close-link
            position: absolute
            top: 16px
            right: 16px
            color: #000
            font-size: 24px
            line-height: 24px
            @media (min-width: 1200px)
              top: 24px
              right: 13px
          .minicart-title
            text-transform: uppercase
            color: $dark-gray
            font-weight: 500
            font-size: 12px
            line-height: 12px
            padding-bottom: 10px
            span
              display: inline-block
              color: $red-color
              margin-left: 5px
          &__empty
            padding: 30px 0
            display: flex
            align-items: center
            justify-content: center
            flex-wrap: wrap
            height: 100%
            font-size: 18px
            line-height: 24px
          &__list
            .product-list
              width: calc(100% + 32px)
              margin: 0 -16px
              padding: 0 16px
              max-height: calc(100vh - 155px)
              overflow-y: scroll
              @include scrollbars(3px, $scrl-bar_track, $scrl-bar_thumb, $scrl-bar_thumbh, 0)
            .minicart-content__item
              display: flex
              align-items: stretch
              justify-content: flex-start
              margin-top: 24px
              padding-bottom: 24px
              border-bottom: 1px solid #f2f2f2
              position: relative
              @media (min-width: 1200px)
                padding-bottom: 19px
              &:last-child
                padding-bottom: 0
                border-bottom: none
              .image-block
                flex: 0 0 99px
                width: 99px
                .product-image
                  display: block
                  width: auto
                  max-width: 100%
                  margin: 0
              .descr-block
                display: flex
                flex-direction: column
                align-items: flex-start
                justify-content: space-between
                flex: 1 1 auto
                width: auto
                max-width: 100%
                padding-left: 17px
                .product-name
                  display: inline-block
                  color: $dark-gray
                  font-weight: 500
                  font-size: 12px
                  line-height: 12px
                  text-transform: uppercase
                  letter-spacing: .02em
                  max-width: calc(100% - 5px)
                  text-overflow: ellipsis
                  overflow: hidden
                  @media (min-width: 1200px)
                    font-size: 14px
                    line-height: 14px
                .article
                  display: block
                  text-transform: uppercase
                  color: $medium-gray
                  font-weight: 500
                  font-size: 12px
                  line-height: 12px
                  letter-spacing: .05em
                  margin-top: 8px
                .properties
                  display: flex
                  align-items: center
                  justify-content: flex-start
                  flex-wrap: wrap
                  margin-top: 12px
                  &__item, &__color
                    flex: 0 0 auto
                    width: auto
                    font-weight: 500
                    font-size: 12px
                    line-height: 12px
                    color: $dark-gray
                  &__color
                    display: flex
                    align-items: center
                    justify-content: flex-start
                    position: relative
                    margin-left: 16px
                    span
                      position: relative
                      width: 16px
                      height: 16px
                      display: inline-block
                      border-radius: 50%
                      border: 3px solid #fff
