@import "settings"
@import "_mixins/_mixins"
@import "_fonts/fonts_list"
@import "_fonts/fontello"                                                                                               // if you use fontello fonts
@import "_components/reset"
@import "_components/header"

/* main page main slider */
.mpage-main
  overflow: hidden
  .main-slider
    position: relative
    .slide-item
      position: relative
      background-size: cover
      background-position: center
      background-repeat: no-repeat
      &:after
        content: ''
        display: block
        width: 100%
        height: 100%
        position: absolute
        top: 0
        left: 0
        background: linear-gradient(0deg, rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3))
      .container
        height: 100vh
        min-height: 620px
        display: flex
        flex-direction: column
        position: relative
        z-index: 1
        @media (min-width: 768px)
          justify-content: center
          padding: 150px 0
      .slide-info
        flex: 1 1 100%
        display: flex
        flex-direction: column
        align-items: center
        justify-content: flex-end
        padding: 150px 0 35%
        text-align: center
        max-width: 400px
        margin: 0 auto
        @media (min-width: 768px)
          max-width: 100%
          padding: 0
          flex: 0 0 auto
          @include adaptive-value('margin-top', 126, 0, 1)
        .slide-title
          color: #fff
          @include adaptive-value('font-size', 88, 38, 1)
          @include adaptive-value('line-height', 88, 38, 1)
          font-weight: 600
          letter-spacing: -.05em
          text-transform: uppercase
          margin-bottom: 25px
          opacity: 0
          transform: translateX(15px)
          transition-delay: 1s
          transition: transform .8s ease-in-out, opacity .8s ease-in-out
          @media (min-width: 768px)
            margin-bottom: 20px
        .slide-subtitle
          color: #fff
          text-transform: uppercase
          @include adaptive-value('font-size', 21, 15.75, 1)
          @include adaptive-value('line-height', 21, 21, 1)
          font-weight: 600
          opacity: 0
          transform: translateX(15px)
          transition: transform .8s ease-in-out, opacity .8s ease-in-out
          @media (min-width: 768px)
            margin: 2px 0 3px
      .slide-links
        flex: 0 0 auto
        padding: 46px 0
        transform: translateY(-15px)
        opacity: 0
        transition: all .8s ease-in-out
        width: 100%
        max-width: 400px
        margin: 0 auto
        @media (min-width: 768px)
          display: flex
          align-items: center
          justify-content: center
          flex-wrap: wrap
          max-width: 100%
          width: 100%
        .slide-btn
          display: block
          width: 100%
          text-align: center
          background-color: #fff
          text-transform: uppercase
          font-weight: 600
          line-height: 14px
          color: $dark-gray
          padding: 16px
          margin-bottom: 14px
          transition: background-color .3s, color .3s
          @media (min-width: 768px)
            flex: 0 0 auto
            width: auto
            margin: 0 8px 14px 8px
            min-width: 235px
          &:hover
            background-color: $dark-gray
            color: #fff
          &:focus
            background-color: $light-gray
            color: $dark-gray
      &.swiper-slide-active
        .slide-info
          .slide-title
            transform: none
            transition-delay: .6s
            opacity: 1
          .slide-subtitle
            transform: none
            transition-delay: 1s
            opacity: 1
        .slide-links
          transform: none
          transition-delay: 1.5s
          opacity: 1
      &.video-slide
        .video-wrapper
          position: absolute
          top: 0
          left: 0
          width: 100%
          height: 100%
          overflow: hidden
          .video-item
            height: auto
            width: auto
            min-width: 100%
            min-height: 100%
            position: absolute
            top: 50%
            left: 50%
            margin-right: -50%
            transform: translate(-50%, -50%)
    .swiper-pagination
      display: flex
      flex-wrap: nowrap
      align-items: center
      justify-content: center
      position: absolute
      bottom: 25px
      left: 0
      width: 100%
      height: auto
      z-index: 1
      .swiper-pagination-bullet
        width: 9px
        height: 9px
        box-shadow: 0 0 0 2px transparent
        border-radius: 50%
        background-color: #fff
        margin: 0 5px
        transition: background-color .3s, box-shadow .3s
        cursor: pointer
        @media (min-width: 768px)
          width: 12px
          height: 12px
          margin: 0 7px
        &-active
          box-shadow: 0 0 0 2px #fff
          background-color: transparent
  & + .full-banner
    @include adaptive-value('margin-top', 32, 15, 1)

